.parent {
  position: relative;
  top: 0;
  left: 0;
}
.image1 {
  position: absolute;
  top: 0;
  left: 0;
  border: 0px skyblue solid;
  z-index: 1;
  width: 15%;
  opacity: 0.6;
  margin-left: 250px;
  margin-top: 50px;
}
.image2 {
  position: absolute;
  top: 0px;
  border: 1px green solid;
  display: flex;
  flex-direction: column;
  padding: 0px;
  margin-left: 0px;
  margin-right: 20px;
  width: 100%;
}
